    init () {
      NewP = this.$refs.NewP
      NewP.addEventListener('scroll', this.scroll, false)
      // console.log(NewP.scrollTop, NewP.clientHeight, NewP.scrollHeight)n
      if (NewP.clientHeight === NewP.scrollHeight) {
