      }
    },
    scroll () {
      // console.log('你进行了滑动')
      // console.log(NewP.scrollTop, NewP.clientHeight, NewP.scrollHeight)
      let nowH = NewP.scrollTop + NewP.clientHeight
      let maxH = NewP.scrollHeight
      if (nowH + 1 > maxH && !this.allInShow) {
        // 显示正在加载
        console.log('开始加载更多资讯')
        this.$Dialog.Rotate({
          ele: NewP
        })
        this.query()
      }
    },
    query () {
      setTimeout(() => {
        this.$Dialog.Rotate({
          ele: NewP,
          state: 'end'
        })
        this.allInShow = true
      }, 1000)
      // this.findByPageN()
    },
    findByPageN () {
      this.$http.findByPageN({
        sectionId: 1,
        page: this.page,
        size: this.size
      }).then((res) => {})
    },
    clickP (data) {

    },
    touchStart (event) {
      this.location = event.changedTouches[0].screenY
    },
    touchMove (event) {
      if (this.location > 0) {
        let loading = this.$refs.loading
        let y = event.changedTouches[0].screenY - this.location
        if (y > 0) {
          // 设置下拉到一定高度时才展示正在加载 之后才获取新数据，防止太灵敏的去获取聊天记录
          if (y > 60) {
            this.loadingShow = true
            if (this.queryOnce) {
              this.queryrefresh()
            }
          }
          y = y > 120 ? 120 : y
          loading.style.height = y / 2 + 'px'
          loading.style.paddingTop = y / 2 + 'px'
        }
      }
    },
    queryrefresh () {
      console.log('刷新消息')
      this.queryOnce = false
      setTimeout(() => {
        this.touchEnd()
      }, 1000)
    },
    touchEnd () {
      this.location = -1
      let loading = this.$refs.loading
      loading.style.height = 0
      loading.style.paddingTop = 0
      this.loadingShow = false
      this.queryOnce = true
    }
  }
}
</script>

<style>
