    text-align: center;
  }
  #newP .content_loading .loading {
    width: 1rem;
    height: 1rem;
    margin: 0.5rem;
    display: inline-block;
    border-radius: 0.5rem;
    background-color: #ff0000;
  }

  #newP .content_loading .one {
    animation: loading 0.5s ease 0.1s infinite alternate;
  }

  #newP .content_loading .two {
    animation: loading 0.5s ease 0.2s infinite alternate;
  }

  #newP .content_loading .three {
