  }

  #newP .content_loading .four {
    animation: loading 0.5s ease 0.4s infinite alternate;
  }

  @keyframes loading {
    from {
      transform: scale(1)
    }

    to {
      transform: scale(0.6);
    }

  }
  /* @keyframes pChange {
